.timeline {
  width: 70vw;
  margin: 0 auto; }
  .timeline body {
    font-family: "Open Sans", sans-serif;
    margin: 0;
    padding: 0 4em; }
  .timeline main {
    min-width: 300px;
    max-width: 400px;
    margin: auto; }
  .timeline li {
    font-size: 1em;
    line-height: 1.5em;
    border-top: 3px solid;
    -webkit-border-image: -webkit-gradient(linear, left top, right top, from(#FA255E), to(#C39EA0));
    -webkit-border-image: linear-gradient(to right, #FA255E 0%, #C39EA0 100%);
            border-image: -webkit-gradient(linear, left top, right top, from(#FA255E), to(#C39EA0));
            border-image: linear-gradient(to right, #FA255E 0%, #C39EA0 100%);
    border-image-slice: 1;
    border-width: 3px;
    margin: 0;
    padding: 40px;
    counter-increment: section;
    position: relative;
    color: black; }
  .timeline .li-l {
    text-align: left; }
  .timeline .li-r {
    text-align: right; }
  .timeline li:nth-child(odd) {
    border-right: 3px solid;
    padding-left: 0; }
    .timeline li:nth-child(odd):before {
      left: 100%;
      margin-left: -20px; }
  .timeline li:nth-child(even) {
    border-left: 3px solid;
    padding-right: 0; }
    .timeline li:nth-child(even):before {
      right: 100%;
      margin-right: -20px; }
  .timeline li:first-child {
    border-top: 0;
    border-top-right-radius: 0;
    border-top-left-radius: 0; }
  .timeline li:last-child {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; }
  .timeline i {
    color: #FA255E; }

